import React from "react";
import { NavLink as Link } from "react-router-dom";
import { withGoogleMap, GoogleMap, Marker } from "react-google-maps";
import PropTypes from "prop-types";

import "./contact.css";

const MapComponent = withGoogleMap(({ lat, lng }) => (
  <GoogleMap defaultZoom={15} defaultCenter={{ lat, lng }}>
    <Marker position={{ lat, lng }} />
  </GoogleMap>
));

const Map = ({ lat, lng }) => (
  <MapComponent
    loadingElement={<div className="map-loading" />}
    containerElement={<div className="map-container" />}
    mapElement={<div className="map-ready" />}
    lat={lat}
    lng={lng}
  />
);

Map.propTypes = {
  lat: PropTypes.number.isRequired,
  lng: PropTypes.number.isRequired
};

const Contact = () => (
  <div id="contact-container">
    <div id="contact-title"></div>
    <div className="contact-content">
      <div className="contact-info">
        <div className="content-title"></div>
        <div className="content-address">78-80</div>
        <div className="content-phone">
          <p>
            <span>+852</span> 2391 6768
          </p>
          <p>
            <span>+852</span> 2789 1538
          </p>
          <p>
            <Link to="mailto://info@wongpowah.com.hk">
              info@wongpowah.com.hk
            </Link>
          </p>
        </div>
      </div>
      <Map lat={22.324302} lng={114.166341} />
    </div>
    <div className="contact-branch"></div>
    <div className="contact-content">
      <div className="contact-info secondary">
        <div className="content-title"></div>
        <div className="content-address">
          
          <br />
          5022
          <br />
           A
          <br />
          2210
          <br />
          518000
        </div>
        <div className="content-phone">
          <p>
            <span>+86 755</span> 2360 4173
          </p>
        </div>
      </div>
      <Map lat={22.529952} lng={114.068913} />
    </div>
    <div className="contact-content">
      <div className="contact-info secondary">
        <div className="content-title"></div>
        <div className="content-address">
          841
          <br />
          
          <br />
          1701
        </div>
        <div className="content-phone">
          <p>
            <span>+8621</span> 6289 6086
          </p>
          <p>
            <span>+8621</span> 6289 6087
          </p>
        </div>
      </div>
      <Map lat={31.223239} lng={121.454261} />
    </div>
  </div>
);

export default Contact;



